import React, {Component} from 'react';
import './Header.css';
import 'tachyons';
import Classes from './classes';

import {Link} from 'react-router-dom'

class Header extends Component{

  render(){
    return(
        <nav className="fixed w-100">
          <ul class="main-nav">
            <Link to="">
              <li>HOME</li>
            </Link>
            <Link to="/about">
              <li>ABOUT</li>
            </Link>
            <Link to="/classes">
              <li>CLASSES</li>
            </Link>
            <Link>
              <li class="push">CONTACT US</li>
            </Link>  
          </ul>
        </nav>
    );

  }

}

export default Header;